<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>售后管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">公司理赔</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>新增公司理赔</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" ref="addFormInline" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini" :rules="rules.company_id" prop="company_id">
          <el-select v-model="params.company_id" placeholder="请选择" @change="companyChange" @focus="companyListInvok" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1?'optionStyle':''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" :rules="rules.customer_name" size="mini" prop="customer_id">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" @focus="clienteleListInvok" @change="customerChange" :loading="loading" remote :remote-method="(query) => { clienteleListInvok(query) }">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li :title="item.address">{{item.address}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" :rules="rules.business_time" prop="business_time">
          <el-date-picker v-model="params.business_time" type="date" placeholder="选择日期" range-separator="-" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="质检员" :rules="rules.employee_name" prop="inspector_id" size="mini">
          <el-select clearable v-model="params.inspector_id" @focus="EmployeeListInvok" placeholder="请选择" filterable>
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="quoteOrder" class="addBtn" size="mini">引用单据</el-button>
      <el-button icon="el-icon-circle-plus-outline" type="primary" :disabled="editType === 1" @click="addOrder" class="addBtn" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class='table_container'>
      <xTable ref='xTable' :edit-rules='editRules' :isEdit='isEdit' :tableColumn='tableColumn' />
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack()">取 消</el-button>
      <el-button type="primary" @click="submit('params')">确 定</el-button>
    </div>
    <appraisalDialog :searchParams="params" v-if='salesDocumentsVisible' @sumbitData='getSumbitData' @close='salesDocumentsVisible = false' />
  </div>
</template>

<script>
import appraisalDialog from "./config/authenticate_dalog.vue";
import { opthionList } from "common/utils/public_function/commonUtils";
import { rowModule, tableHead } from "./config/companyclaim_add";
export default {
  name: "aftersaleCompanyclaimAdd",
  mixins: [tableHead],
  data() {
    return {
      customer: {
        options: []
      },
      getEmployeeList: {
        options: []
      },
      loading:false,
      editType: 2, // 1为引用 2 为 增行
      params: {},
      salesDocumentsVisible: false //来源单号表弹框
    };
  },
  components: {
    appraisalDialog
  },
  methods: {
    customerChange(val) {
      this.$set(
        this.params,
        "customer_name",
        this.customerList.filter(item => item.customer_id == val)[0]
          .customer_name
      );
    },
    // 引用公司鉴定单
    getSumbitData(data) {
      this.customerList = [data.customerList];
      this.editType = 1;
      this.salesDocumentsVisible = false;
      this.$refs.xTable._proxy(
        "loadData",
        data.goods_info.map(val => {
          val.intube_price = val.inflap_price; // 内垫费字段不一样
          val.abrasion_price = val.wear_price; // 磨损费字段不一样
          var box = val.symptom_id_str && val.symptom_id_str.split(",");
          val.symptom_id = box.map(val => Number(val));
          val.tire_check_status = Number(val.tire_check_status);
          val.company_status = Number(val.company_status);
          var row = this.$publicFution.filter(rowModule, val);
          // computeDeep({ row: row, column: { property: "raw_flowerdeep" } });
          return row;
        })
      );
    },
    addOrder(step) {
      if (this.params.customer_id || step === true) {
        this.editType = 2;
        this.$refs.xTable._proxy(
          "insertAt",
          this.$publicFution.deepCopy(rowModule),
          -1
        );
      } else {
        this.$message({
          message: "请先选择客户名称",
          type: "warning"
        });
      }
    },
    companyChange() {
      this.$refs.xTable._proxy("loadData", []);
      this.$set(this.params, "department_id", null);
      this.$set(this.params, "customer_id", null);
    },
    goBack() {
      this.$publicFution.goPage();
      this.$router.push({
        path: "/aftersale/companyclaim"
      });
    },
    quoteOrder() {
      if (this.params.customer_id) {
        this.salesDocumentsVisible = true;
      } else {
        this.$message({
          message: "请先选择客户名称",
          type: "warning"
        });
      }
    },
    submit() {
      var tableBox = this.$publicFution.deepCopy(
        this.$refs.xTable._proxy("getTableData").fullData
      );
      if (tableBox.length && !tableBox[tableBox.length - 1].goods_name) {
        tableBox.pop();
      }
      if (tableBox.length === 0) {
        this.$message.warning("请完善表格数据");
        return;
      }
      this.$refs.xTable._proxy(
        "loadData",
        this.$publicFution.deepCopy(tableBox)
      );
      this.$refs["addFormInline"].validate(valid => {
        if (valid) {
          this.$refs.xTable._proxy("validate").then(() => {
            var params = this.$publicFution.deepCopy(this.params);
            var submitData = tableBox.map(val => {
              val.symptom_id = val.symptom_id.join(",");
              return val;
            });
            params.goods_json = JSON.stringify(submitData);
            this.$axMethod.addClaim(params).then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.msg ? res.data.msg : res.data.data,
                  type: "success"
                });
                this.goBack();
              }
            });
          });
        }
      });
    }
  },
  mounted() {
    this.getNEwlist(); // 获取病象

    let { employee_id, company_admin_id } = localStorage;
    if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id)
      });
    } else {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id),
        inspector_id: Number(localStorage.employee_id),
        department_id: Number(localStorage.department_id)
      });
    }
    this.addOrder(true);
    opthionList(this, localStorage);
  }
};
</script>

<style lang="less" scoped>
.table_container {
  /deep/ .longSymptom {
    height: 25px;
    width: 200px;
    .el-select__tags {
      margin-top: -2px;
      .el-tag:first-child {
        .el-select__tags-text {
          display: inline-block;
          width: 80px;
          vertical-align: middle;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
    .el-input__inner {
      height: 24px !important;
      margin-top: -1px;
    }
  }
}
</style>
