<template>
  <div>
    <el-dialog :close-on-click-modal="false" title="引用公司鉴定单据" :visible="true" class="dialogForm" width="1300px" @close="close">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="审核状态" size="mini">
            <span>已审核</span>
          </el-form-item>
          <br>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
            </el-date-picker>
          </el-form-item>
          <el-form-item label="客户名称" size="mini">
            <el-select v-model="params.customer_id" filterable placeholder="请输入" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="胎号" size="mini">
            <el-input v-model="params.tire_un" class="shortSearchInput" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" class="shortSearchInput" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="公司鉴定单据号" size="mini">
            <el-input v-model="params.company_appraisal_sn" class="shortSearchInput" clearable @keyup.enter.native="onSearch(1)" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="公司鉴定结果" size="mini">
            <el-select clearable v-model="params.company_status">
              <el-option label="待厂检" :value="1">待厂检</el-option>
              <el-option label="理赔" :value="2">理赔</el-option>
              <!-- <el-option label="不理赔" value="3">不理赔</el-option> -->
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <xTable :ajax='{ url: "getAuthenticateList", params: params }' ref='xTable' :tableColumn='tableColumn' @selectChange='selectChange' />
      <div slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button class="tx" type="primary" @click="dialogSubmitForm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/sever/api";
import { tableHead } from "./authenticate_dalog";
export default {
  mixins: [tableHead],
  data() {
    return {
      authority: {},
      paramsModule: {
        company_status: 2
      },
      params: {},
      loading: false,
      timeValue: []
    };
  },
  props: ["searchParams"],
  methods: {
    onSearch(number) {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.$refs.xTable._getData(number)
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.params = Object.assign({}, this.paramsModule);
      this.onSearch(1);
    },
    close() {
      this.$emit("close");
    },
    // 复选框
    selectChange(row) {
      this.multipleSelection = row.selection;
    },
    dialogSubmitForm() {
      let addr = [];
      for (var i in this.multipleSelection) {
        addr.push(this.multipleSelection[i].company_appraisal_goods_id);
      }
      if (addr.length === 0) {
        this.$message.warning("请选择单据！");
        return;
      }
      this.$axios({
        method: "post",
        url: api.claimCompanyList,
        data: {
          company_appraisal_goods_id: addr.join(",")
        }
      })
        .then(res => {
          if (res.data.code == 200) {
            this.$emit("sumbitData", {
              customerList: {
                customer_id: res.data.data.data[0].customer_id,
                customer_name: res.data.data.data[0].customer_name,
                contact_name: "",
                customer_mobile: "",
                address: ""
              },
              goods_info: res.data.data.data
            });
          }
        })
        .catch(() => {
          this.$emit("sumbitData", {});
        });
    }
  },
  created() {
    this.params = Object.assign({}, this.paramsModule, {
      customer_id: this.searchParams.customer_id
    });
    this.$set(this, "customerList", [
      {
        customer_id: this.searchParams.customer_id,
        customer_name: this.searchParams.customer_name
      }
    ]);
  }
};
</script> 